body {
  min-width: 320px;
  color: #3F566E;
  background: #fff;
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.43;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.is-overflow-hidden {
  overflow: hidden;
}

[data-anim-from] {
  position: relative;
  opacity: 0;
}

[data-anim-from="bottom"] {
  transform: translateY(50px);
}

.text-yellow {
  color: #FFDC45;
}

.bla {
  position: relative;
  overflow: hidden;
}

.bla__in {
  float: left;
  height: 300px;
  width: 400px;
  border: 1px solid red;
}

.title-bigger {
  font-family: "Arbutus Slab", Helvetica, Arial, sans-serif;
  font-size: 100px;
  letter-spacing: 0;
  line-height: 0.76;
}

@media only screen and (max-width: 767px) {
  .title-bigger {
    font-size: 70px;
  }
}

.title-lg {
  font-family: "Arbutus Slab", Helvetica, Arial, sans-serif;
  font-size: 75px;
  letter-spacing: 0;
  line-height: 1;
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 42px;
  }
}

.out {
  overflow: hidden;
  min-height: 100vh;
}

.inner {
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.block {
  display: flex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 47px;
}

@media only screen and (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.header__nav {
  position: relative;
  z-index: 1000;
  float: right;
  margin-right: -7px;
  width: 31px;
  height: 31px;
  background: none;
}

.header__nav:before, .header__nav:after {
  content: '';
  position: absolute;
  right: 7px;
  width: 17px;
  height: 3px;
  background-color: #3F566E;
  transform-origin: right center;
  transition: transform 0.25s, background-color .25s;
}

.header_mod .header__nav:before, .header_mod .header__nav:after {
  background-color: #FFDC45;
}

.header__nav.is-active:before {
  transform: translate3d(0px, -3px, 0) rotate(-45deg) scaleX(1.4);
  background-color: #3F566E;
}

.header__nav.is-active:after {
  transform: translate3d(0px, 2px, 0) rotate(45deg) scaleX(1.4);
  background-color: #3F566E;
}

.header__nav:before {
  top: 8px;
}

.header__nav:after {
  bottom: 8px;
}

.logo {
  position: relative;
  z-index: 1000;
  display: inline-block;
  vertical-align: top;
  font-size: 31px;
}

.logo__icon {
  display: block;
  fill: #3F566E;
  transition: fill .25s;
}

.header_mod .logo__icon {
  fill: #fff;
}

.header.is-open .logo__icon {
  fill: #3F566E;
}

.nav {
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .nav {
    text-align: left;
  }
}

.nav__item:not(:last-child) {
  margin-bottom: 5px;
}

.nav__link {
  display: block;
  color: #3F566E;
  font-family: "Arbutus Slab", Helvetica, Arial, sans-serif;
  font-size: 70px;
  font-weight: 400;
  line-height: 76px;
  text-transform: lowercase;
  transition: color 0.25s;
}

@media only screen and (max-width: 1023px) {
  .nav__link {
    font-size: 60px;
    line-height: 1.1;
  }
}

@media only screen and (max-width: 767px) {
  .nav__link {
    font-size: 50px;
  }
}

.no-touch .nav__link:hover {
  color: #FFDC45;
}

.nav__link.is-active {
  color: #FFDC45;
}

.menu {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-top: 100px;
  min-width: 320px;
  background: rgba(250, 250, 250, 0.96);
  box-shadow: 0 24px 55px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  font-size: 0;
  transform: translate3d(0, -110%, 0);
  transition: transform 0.3s ease-in-out;
}

.menu:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@supports (backdrop-filter: blur(11px)) {
  .menu {
    background: rgba(250, 250, 250, 0.69);
    backdrop-filter: blur(11px);
  }
}

@media only screen and (max-width: 767px) {
  .menu {
    padding-top: 71px;
  }
}

.menu.is-open {
  transform: translate3d(0, 0, 0);
}

.menu.is-contacts .menu__nav {
  display: none;
}

.menu.is-contacts .menu__contacts {
  display: block;
}

.menu.is-contacts .menu__link_contacts {
