}

.menu__center {
  display: inline-block;
  vertical-align: middle;
  overflow: auto;
  padding-bottom: 78px;
  width: 100%;
  max-height: 100%;
}

@media only screen and (max-width: 767px) {
  .menu__center {
    padding-bottom: 20px;
  }
}

.menu__content {
  position: relative;
  float: left;
  width: 50%;
  text-align: left;
}

@media only screen and (max-width: 1023px) {
  .menu__content {
    width: 60%;
  }
}

@media only screen and (max-width: 767px) {
  .menu__content {
    width: 100%;
  }
}

.menu__nav, .menu__contacts {
  float: right;
  width: 50%;
}

@media only screen and (max-width: 1023px) {
  .menu__nav, .menu__contacts {
    width: 40%;
  }
}

@media only screen and (max-width: 767px) {
  .menu__nav, .menu__contacts {
    float: none;
    padding-bottom: 50px;
    width: 100%;
  }
}

.menu__contacts {
  display: none;
}

.menu__info {
  padding-bottom: 22px;
}

.menu__icon {
  margin-bottom: 7px;
}

.menu__subtitle {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
