.details {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include sm {
		display: block; }
	&__figure,
	&__content {
		flex-grow: 1;
		width: 50%;
		@include sm {
			width: 100%; } }
	&__figure {
		position: relative;
		z-index: -1;
		order: 1;
		padding-top: 27px;
		margin-bottom: -26px;
		font-size: 0;
		text-align: center;
		@include sm {
			position: relative;
			margin: 0; }
		img {
			max-width: 369px;
			@include md {
				max-width: 300px; } } }
	&__content {
		padding: 50px 0 40px; }
	&__header {
		position: relative;
		padding-left: 35px;
		margin-bottom: 48px;
		max-width: 450px;
		@include sm {
			padding-left: 20px;
			margin-bottom: 30px;
			max-width: 320px; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			bottom: 6px;
			border-left: 4px solid $yellow; } }
	&__label {
		font-family: $montserrat;
		@include f(25px, 300, 21px);
		letter-spacing: 4.17px; }
	&__title {
		font-family: $arbutus;
		@include f(70px, 400, 66px);
		@include md {
			@include f(60px, null, 1); }
		@include sm {
			font-size: 48px; } }
	&__description {
		padding-left: 37px;
		font-family: $lato;
		@include f(14px, 700, 20px);
		@include sm {
			padding-left: 0; } } }
