.banner {
	padding: 120px 0 145px;
	background: $yellow;
	@include md {
		padding: 100px 0; }
	@include sm {
		padding: 50px 0; }
	&__label {
		padding: 0 3px;
		color: $dark;
		font-family: $montserrat;
		@include f(20px, 300, 1.2);
		letter-spacing: 0.57px;
		text-transform: uppercase; }
	&__title {
		color: $white;
		font-family: $montserrat;
		@include f(70px, 700, 60px);
		letter-spacing: 2px;
		@include sm {
			@include f(48px, null, 1); }
		&_sm {
			@include f(62px, 700, 60px);
			@include sm {
				@include f(46px, null, 1); } } }
	&__inner {
		display: flex;
		flex-direction: row;
		align-items: flex-end;
		@include md {
			display: block;
			text-align: center; } }
	&__content {
		flex-grow: 1;
		padding-right: 15px;
		@include md {
			padding: 0 0 20px; } }
	&__btn {
		padding: 5px 0;
		width: 313px;
		@include md {
			margin: 0 auto;
			width: auto;
			max-width: 300px; }
		.btn {
			box-shadow: 0 13px 37px 0 rgba(0,0,0,0.12); } } }
